    { link: '/', title: 'Strona główna' },
    { link: '/about', title: 'O nas' },
    { link: '/articles', title: 'Artykuły' },
    // { link: '/sections', title: 'Sekcje' },
    { link: '/projects', title: 'Projekty' },
    // { link: '/hardware', title: 'Hardware' },
    // { link: '/profiles', title: 'Profile' },
