  public isVisible()
  {
      if(this.$refs.hamburgerMenuIcon) {
          const d = this.$refs.hamburgerMenuIcon as HTMLElement;
          console.log(d);
          return d.style.display;
      }
  }

  get computedSidebar() {
      console.log("COMPUTED SIDEBAR");
      if(this.$refs.hamburgerMenuIcon) {
          const d = this.$refs.hamburgerMenuIcon as HTMLElement;
          console.log(d);
          return d.style.display;
